/*
    @LCard

    @task : main-holder for the half-screen container

    @argument 

    props.background : the desired background
*/

// react hooks & libraries
import React from 'react'

// css import
import styles from './LCard.module.css'
// import color from '../../Res/ColorsString'

// components import


const LCard = (props) => {

    return <div className={`${styles.lcard} ${props.className}`} 
        style={{
        background: props.background
    }}>
        {props.children}
    </div>
}

export default LCard