// react hooks & libraries
import React from 'react'
import {Route, Switch, Redirect} from 'react-router-dom'
// css import

// components import
import HomePage from './HomePage/HomePage'
import CreatePostPage from './CreatePostPage/CreatePostPage'
import Paths from './Res/Paths'
// import NavigationContext from './VarStore/NavigationContext'



function App() {

  return (
    <div className="App">
      {/* <HomePage /> */}
      {/* <CreatePostPage /> */}
      <Switch>
        <Route path={Paths.HOMESITE} exact>
          <Redirect to={Paths.HOMEPAGE}/>
        </Route>
        <Route path={Paths.HOMEPAGE}>
          <HomePage />
        </Route>
        <Route path={Paths.CREATE_POST_PAGE}>
          <CreatePostPage />
        </Route>
      </Switch>
    </div>
  );
}

export default App;
