// react hooks & libraries
import React from 'react'

// css import 
import styles from './NavigationItem.module.css'
// component import

const NavigationItem = (props) => {
    
    return <li
        className={styles['list-item']}
        style={{
            color: props.color
        }}
    >
        <a href={props.href}>{props.name}</a>
    </li>
}

export default NavigationItem