/*
    @LeftFrame

    @task1: add page icon
    @task2: blog title input
    @task3: subtitle input
    @task4: support breaker line

    @task5: name input
    @task6: designation input
*/

// react hooks & libraries
import React, {useState, useReducer, useCallback} from 'react'

// css import
import styles from './LeftFrame.module.css'
import color from '../../Res/ColorsString'
import BubbleIcon from '../../Res/SvgCompnents/BubbbleIcon'
import HorizontalStroke from '../../Res/SvgCompnents/HorizontalStroke'

// components import
import Card from '../UI/Card'








/*
    @titleReducer

    @description
    manages the input title
*/

const initTitleState = {
    defaultTitle: "BLOG TITLE",
    placeholder: "BLOG TITLE",
    isValid: true,
    className: `${styles['upper-body-text-input']}`
}

const titleReducer = (state, action) => {
    switch (action.type) {
        case 'VALID': {
            return {
                defaultTitle: action.newValue,
                placeholder: "BLOG TITLE",
                isValid: true,
                className: `${styles['upper-body-text-input']}`
            }
        }
        case 'INVALID': {
            return {
                defaultTitle: "BLOG TITLE",
                placeholder: "*TITLE REQUIRED",
                isValid: false,
                className: `${styles['upper-body-text-input']} ${styles.invalid}`
            }
        }
        default: {
            return initTitleState;    
        }
    }
}





/*
    @nameReducer

    @description
    managest the input name
*/


const initNameState = {
    defaultName: "AUTHOR NAME",
    placeholder: "AUTHOR NAME",
    isValid: true,
    className: `${styles['lower-body-text-input']}`
}

const nameReducer = (state, action) => {
    switch (action.type) {
        case 'VALID': {
            return {
                defaultName: action.newValue,
                placeholder: "AUTHOR NAME",
                isValid: true,
                className: `${styles['lower-body-text-input']}`
            }
        }
        case 'INVALID': {
            return {
                defaultName: "AUTHOR NAME",
                placeholder: "*NAME REQUIRED",
                isValid: false,
                className: `${styles['lower-body-text-input']} ${styles.invalid}`
            }
        }
        default: {
            return initNameState;
        }
    }
}






/*
    @designationReducer

    @description 
    manages the designation input
*/


const initDesignationState = {
    defaultDesignation: "DESIGNATION",
    placeholder: "DESIGNATION",
    isValid: true,
    className: `${styles['lower-body-text-input']}`
}

const designationReducer = (state, action) => {
    switch (action.type) {
        case 'VALID': {
            return {
                defaultDesignation: action.newValue,
                placeholder: "DESIGNATION",
                isValid: true,
                className: `${styles['lower-body-text-input']}`
            }
        }
        case 'INVALID': {
            return {
                defaultDesignation: "DESIGNATION",
                placeholder: "*DESIGNATION REQUIRED",
                isValid: false,
                className: `${styles['lower-body-text-input']} ${styles.invalid}`
            }
        }
        default: {
            return initDesignationState;
        }
    }
}







const LeftFrame = () => {

    const defaultSubTitle = "DESCRIPTION (OPTIONAL)"

    // const [ title, setTitle ] = useState(defaultTitle)
    const [ title, dispatchTitle] = useReducer(titleReducer, initTitleState)
    const [ subTitle, setSubTitle ] = useState(defaultSubTitle)
    const [name, dispatchName] = useReducer(nameReducer, initNameState)
    const [designation, dispatchDesignation] = useReducer(designationReducer, initDesignationState)
    
    const titleStoreHandlerCallback = useCallback(event => {
        if (event.target.value.toUpperCase() === initTitleState.defaultTitle || event.target.value === "") {
            event.target.value = ""
            dispatchTitle({ type: 'INVALID' })
            return;
        } else {
            dispatchTitle({ type: 'VALID', newValue: event.target.value })
        }
    },[])

    

    const subTitleStoreHandlerCallback = useCallback(event => {
        if (event.target.value.toUpperCase() === defaultSubTitle || event.target.value === "") {
            event.target.value = ""
            return;
        }

        setSubTitle(event.target.value)
    },[])

    const nameStoreHandlerCallback = useCallback(event => {
        if (event.target.value.toUpperCase() === initNameState.defaultName || event.target.value === "") {
            event.target.value = ""
            dispatchName({type: "INVALID"})
            return;
        } else {
            dispatchName({type: "VALID", newValue: event.target.value})
        }

    },[])

    const designationStoreHandlerCallback = useCallback(event => {
        if (event.target.value.toUpperCase() === initDesignationState.defaultDesignation || event.target.value === "") {
            event.target.value = ""
            dispatchDesignation({type: "INVALID"})
            return;
        } else {
            dispatchDesignation({type: "VALID", newValue: event.target.value})
        }

    },[])

    return <Card background={color.rich_black} className={styles['main-holder']}>

        {/* TODO: Add upper content */}
        
        <div className={styles['upper-body']}> 
            <BubbleIcon />

            <div
                className={styles['upper-body-text']}
                style={{
                    color: color.yellow
                }}
            >
                
                <input
                    // className={styles['upper-body-text-input']}
                    className={title.className}
                    style={{
                        color: color.yellow
                    }}
                    type="text"
                    autoComplete="none"
                    required={true}
                    placeholder={title.placeholder}
                    onBlur={titleStoreHandlerCallback}
                />
                
                <input
                    className={styles['upper-body-text-input']}
                    style={{
                        color: color.yellow,
                        fontSize: "36px"
                    }}
                    type="text"
                    autoComplete="none"
                    placeholder={defaultSubTitle}
                    onBlur={subTitleStoreHandlerCallback}
                />
                
            </div>
                
            <div className={styles['upper-body-end']}>
                <HorizontalStroke />
            </div>
            
        </div>


        {/* TODO: Add lower content */}
        
        <div className={styles['lower-body']}>

            <div
                className={styles['lower-body-text']}
                style={{
                    color: color.yellow
                }}
            >
                <input
                    // className={styles['lower-body-text-input']}
                    className={name.className}
                    style={{
                        color: color.yellow
                    }}
                    type="text"
                    autoComplete="none"
                    required={true}
                    placeholder={name.placeholder}
                    onBlur={nameStoreHandlerCallback}
                />
                <input
                    // className={styles['lower-body-text-input']}
                    className={designation.className}
                    style={{
                        color: color.yellow,
                        fontSize: "18px"
                    }}
                    type="text"
                    autoComplete="none"
                    required={true}
                    placeholder={designation.placeholder}
                    onBlur={designationStoreHandlerCallback}
                />
                
            </div>

        </div>

    </Card>
}

export default LeftFrame

