// react hooks & libraries
import React, {useState, useCallback, useEffect} from 'react'

// css import
import styles from './RightFrame.module.css'
import color from '../../Res/ColorsString'
import CurieuxBlueLogo from '../../Res/SvgCompnents/CurieuxBlueLogo'
import ImageInputIcon from '../../Res/SvgCompnents/ImageInputIcon'
import ContentInputIcon from '../../Res/SvgCompnents/ContentInputIcon'
import ArrowDownSmall from '../../Res/SvgCompnents/ArrowDownSmall'

// components import
import Card from '../UI/Card'
