    const STREAM_DROPDOWN_ID = "create-post-dropdown"
    const STREAM_DROPDOWN_DIALOG_ID = "drop-down-dialog"

    // dom elements
    const [dropdownDOM, setDropdownDOM] = useState(document.getElementById(STREAM_DROPDOWN_ID))
    const [dropdownDialogDOM, setDropdownDialogDOM] = useState(document.getElementById(STREAM_DROPDOWN_DIALOG_ID))
    const [arrowDownColor,setArrowDownColor] = useState(color.gray)
    
    useEffect(() => {
        setDropdownDOM(document.getElementById(STREAM_DROPDOWN_ID)) 
        setDropdownDialogDOM((document.getElementById(STREAM_DROPDOWN_DIALOG_ID)))
    },[])

    

    // animations controlling
    // TODO: open drop-down 
